import { create } from 'zustand';
import CartService from '../services/cart.service';
import useAuthStore from './useAuthStore';

const useCartStore = create((set, get) => ({
  items: [],
  subtotal: 0,
  tax: 0,
  total: 0,
  isLoading: false,

  // Calculate totals
  calculateTotals: () => {
    const items = get().items;
    const subtotal = items.reduce((sum, item) => sum + (item.price * item.quantity), 0);
    const tax = subtotal * 0.08; // 8% tax
    const total = subtotal + tax;
    set({ subtotal, tax, total });
  },

  // Set cart
  setCart: (cart) => {
    const items = cart.items || [];
    const subtotal = cart.subtotal || items.reduce((sum, item) => sum + (item.price * item.quantity), 0);
    const tax = cart.tax || subtotal * 0.08;
    const total = cart.total || subtotal + tax;
    
    set({
      items,
      subtotal,
      tax,
      total
    });
  },

  // Fetch cart from server
  fetchCart: async () => {
    const isAuthenticated = useAuthStore.getState().isAuthenticated;
    if (!isAuthenticated) {
      const localCart = CartService.getLocalCart();
      console.log('Loading local cart:', localCart);
      get().setCart(localCart);
      return;
    }

    set({ isLoading: true });
    try {
      const cart = await CartService.getCart();
      console.log('Fetched cart from server:', cart);
      get().setCart(cart);
      set({ isLoading: false });
    } catch (error) {
      set({ isLoading: false });
      console.error('Failed to fetch cart:', error);
      console.error('Error details:', error.response?.data);
    }
  },

  // Add to cart
  addToCart: async (product, quantity = 1) => {
    const isAuthenticated = useAuthStore.getState().isAuthenticated;

    if (!isAuthenticated) {
      const cart = CartService.addToLocalCart(product, quantity);
      console.log('Added to local cart:', cart);
      get().setCart(cart);
      return;
    }

    set({ isLoading: true });
    try {
      const cart = await CartService.addToCart(product._id, quantity);
      console.log('Added to cart response:', cart);
      get().setCart(cart);
      set({ isLoading: false });
    } catch (error) {
      set({ isLoading: false });
      console.error('Add to cart error in store:', error);
      throw error;
    }
  },

  // Update cart item
  updateCartItem: async (productId, quantity) => {
    const isAuthenticated = useAuthStore.getState().isAuthenticated;

    if (!isAuthenticated) {
      // Handle local cart update
      const cart = CartService.getLocalCart();
      const itemIndex = cart.items.findIndex(item => item.productId === productId);
      if (itemIndex > -1) {
        cart.items[itemIndex].quantity = quantity;
        CartService.saveLocalCart(cart);
        get().setCart(cart);
      }
      return;
    }

    set({ isLoading: true });
    try {
      const cart = await CartService.updateCartItem(productId, quantity);
      get().setCart(cart);
      set({ isLoading: false });
    } catch (error) {
      set({ isLoading: false });
      throw error;
    }
  },

  // Remove from cart
  removeFromCart: async (productId) => {
    const isAuthenticated = useAuthStore.getState().isAuthenticated;

    if (!isAuthenticated) {
      const cart = CartService.removeFromLocalCart(productId);
      get().setCart(cart);
      return;
    }

    set({ isLoading: true });
    try {
      const cart = await CartService.removeFromCart(productId);
      get().setCart(cart);
      set({ isLoading: false });
    } catch (error) {
      set({ isLoading: false });
      throw error;
    }
  },

  // Clear cart
  clearCart: async () => {
    const isAuthenticated = useAuthStore.getState().isAuthenticated;

    if (!isAuthenticated) {
      CartService.clearLocalCart();
      set({ items: [], subtotal: 0, tax: 0, total: 0 });
      return;
    }

    set({ isLoading: true });
    try {
      await CartService.clearCart();
      set({ items: [], subtotal: 0, tax: 0, total: 0, isLoading: false });
    } catch (error) {
      set({ isLoading: false });
      throw error;
    }
  },

  // Sync cart after login
  syncCart: async () => {
    const localCart = CartService.getLocalCart();
    if (localCart.items.length > 0) {
      try {
        const cart = await CartService.syncCart(localCart.items);
        get().setCart(cart);
        CartService.clearLocalCart();
      } catch (error) {
        console.error('Failed to sync cart:', error);
      }
    } else {
      await get().fetchCart();
    }
  },

  // Get item count
  getItemCount: () => {
    return get().items.reduce((sum, item) => sum + item.quantity, 0);
  }
}));

export default useCartStore;
